import { MAGIC_TRADITIONS } from './spell'
import { setHasElement } from './utils'

const dcByLevel = new Map<number, number>()
dcByLevel.set(-1, 13)
dcByLevel.set(0, 14)
dcByLevel.set(1, 15)
dcByLevel.set(2, 16)
dcByLevel.set(3, 18)
dcByLevel.set(4, 19)
dcByLevel.set(5, 20)
dcByLevel.set(6, 22)
dcByLevel.set(7, 23)
dcByLevel.set(8, 24)
dcByLevel.set(9, 26)
dcByLevel.set(10, 27)
dcByLevel.set(11, 28)
dcByLevel.set(12, 30)
dcByLevel.set(13, 31)
dcByLevel.set(14, 32)
dcByLevel.set(15, 34)
dcByLevel.set(16, 35)
dcByLevel.set(17, 36)
dcByLevel.set(18, 38)
dcByLevel.set(19, 39)
dcByLevel.set(20, 40)
dcByLevel.set(21, 42)
dcByLevel.set(22, 44)
dcByLevel.set(23, 46)
dcByLevel.set(24, 48)
dcByLevel.set(25, 50)

const dcAdjustments = new Map<DCAdjustment, number>()
dcAdjustments.set('incredibly easy', -10)
dcAdjustments.set('very easy', -5)
dcAdjustments.set('easy', -2)
dcAdjustments.set('normal', 0)
dcAdjustments.set('hard', 2)
dcAdjustments.set('very hard', 5)
dcAdjustments.set('incredibly hard', 10)

export function calculateDC(level: number, { proficiencyWithoutLevel, rarity = 'common' }: DCOptions = {}): number {
    const pwlSetting = game.settings.get('pf2e', 'proficiencyVariant')
    proficiencyWithoutLevel ??= pwlSetting === 'ProficiencyWithoutLevel'

    // assume level 0 if garbage comes in. We cast level to number because the backing data may actually have it
    // stored as a string, which we can't catch at compile time
    const dc = dcByLevel.get(level) ?? 14
    if (proficiencyWithoutLevel) {
        // -1 shouldn't be subtracted since it's just
        // a creature level and not related to PC levels
        return adjustDCByRarity(dc - Math.max(level, 0), rarity)
    } else {
        return adjustDCByRarity(dc, rarity)
    }
}

export function adjustDCByRarity(dc: number, rarity: Rarity = 'common'): number {
    return adjustDC(dc, rarityToDCAdjustment(rarity))
}

export function adjustDC(dc: number, adjustment: DCAdjustment = 'normal'): number {
    return dc + (dcAdjustments.get(adjustment) ?? 0)
}

export function rarityToDCAdjustment(rarity: Rarity = 'common'): PositiveDCAdjustment {
    switch (rarity) {
        case 'uncommon':
            return 'hard'
        case 'rare':
            return 'very-hard'
        case 'unique':
            return 'incredibly-hard'
        default:
            return 'normal'
    }
}
export function getDcByLevel(actor: ActorPF2e) {
    const level = Math.clamped(actor.level, -1, 25)
    return dcByLevel.get(level)!
}

function getDcRarity(item: PhysicalItemPF2e): Rarity {
    return item.traits.has('cursed') ? 'unique' : item.rarity
}

function getMagicTraditions(item: PhysicalItemPF2e): Set<MagicTradition> {
    const traits: string[] = item.system.traits.value
    return new Set(traits.filter((t): t is MagicTradition => setHasElement(MAGIC_TRADITIONS, t)))
}

function getIdentifyMagicDCs(item: PhysicalItemPF2e, baseDC: number, notMatchingTraditionModifier: number): IdentifyMagicDCs {
    const result = {
        occult: baseDC,
        primal: baseDC,
        divine: baseDC,
        arcane: baseDC,
    }
    const traditions = getMagicTraditions(item)
    for (const key of MAGIC_TRADITIONS) {
        // once an item has a magic tradition, all skills
        // that don't match the tradition are hard
        if (traditions.size > 0 && !traditions.has(key)) {
            result[key] = baseDC + notMatchingTraditionModifier
        }
    }
    return { arcana: result.arcane, nature: result.primal, religion: result.divine, occultism: result.occult }
}

export function getItemIdentificationDCs(
    item: PhysicalItemPF2e,
    { proficiencyWithoutLevel = false, notMatchingTraditionModifier }: IdentifyItemOptions
): GenericIdentifyDCs | IdentifyMagicDCs | IdentifyAlchemyDCs {
    const baseDC = calculateDC(item.level, { proficiencyWithoutLevel })
    const rarity = getDcRarity(item)
    const dc = adjustDCByRarity(baseDC, rarity)
    if (item.isMagical) {
        return getIdentifyMagicDCs(item, dc, notMatchingTraditionModifier)
    } else if (item.isAlchemical) {
        return { crafting: dc }
    } else {
        return { dc: dc }
    }
}
